.sliderTitle {
  font-size: 22px;
  width: 100%;
  height: 35px;
  overflow-x: scroll;
  scroll-behavior: smooth;
  color: black;
}

.sliderDate {
  font-size: 14px;
  width: 100%;
  height: 18px;
  overflow-x: scroll;
  scroll-behavior: smooth;
  color: black;
  margin-top: 0px;
}

.sliderVote {
  font-size: 14px;
  width: 100%;
  height: 18px;
  overflow-x: scroll;
  scroll-behavior: smooth;
  color: black;
  margin-top: -10px;
}

.thumbSlider {
  height: 30vmax;
  width: 100%;
  display: flex;
  overflow-x: scroll;
}

.thumbCard {
  display: inline-flex;
  padding: 5px;
  width: 26vw;
  height: 18vmax;
}

.thumbImg {
  width: auto;
  height: 17vmax;
  object-fit: cover;
  border-radius: 10px;
}

.thumbTitle {
  font-size: 16px;
  width: 100%;
  height: 18px;
  overflow-x: scroll;
  scroll-behavior: smooth;
  color: black;
}

.thumbDate {
  font-size: 1.2vmax;
  color: black;
  margin-top: -1px;
}

.thumbVote {
  font-size: 1.5vmax;
  color: gold;
  margin-top: -1px;
}

.noScrollbar::-webkit-scrollbar {
  display: none;
}

.movieBackDrop {
  height: 35vh;
  width: auto;
  object-fit: cover;
  position: relative;
}

.BackDropBox{
  height: 35vh;
  width: auto;
}
.moviePoster {
  height: 25vh;
  position: relative;
  margin-top: -23vh;
  object-fit: contain;
  border-radius: 10px;
  width: auto;
}

.trailers {
  display: flex;
  scroll-behavior: smooth;
  overflow-x: scroll;
}

.TrailerThumb {
  border-radius: 10px;
  position: static;
}

.Slides {
  width: fit-content;
}

.centerItem {
  justify-content: center;
  text-align: center;
}